:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

a {
  color: #000;
  text-decoration: none;
}

body {
  padding: 20px;
}

h2 {
  margin: 0;
  padding: 0;
}

.category-title {
  letter-spacing: -.04em;
  color: #111;
  margin-top: 40px;
  font-family: DM Sans;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 38px;
}

.category-title--highlight {
  color: #4f2ee8;
}

.bestSellers__container {
  max-width: 335px;
  margin: 0 auto;
}

.shop-card {
  flex-wrap: wrap;
  margin-bottom: 40px;
  display: flex;
}

.shop-card__category {
  letter-spacing: .03em;
  text-transform: uppercase;
  color: #b4afaf;
  margin-top: 40px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.image-block__image {
  width: 335px;
  border-radius: 8px;
