}

.shop-card__meta {
  width: 100%;
  flex-direction: column;
  gap: 4px;
  margin-top: 14px;
  display: flex;
}

.shop-card__title {
  letter-spacing: -.04em;
  text-transform: uppercase;
  color: #111;
  font-family: DM Sans;
  font-size: 16px;
