  color: #b4afaf;
  font-family: DM Sans;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
}

.shop-image__text {
  letter-spacing: -.04em;
  color: #111;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.button {
  width: 124px;
  height: 46px;
  letter-spacing: -.01em;
  text-transform: uppercase;
